import React from "react";
import { toast } from "react-toastify";
import { api, signinapi } from "api";
import { AUTH_USER, AUTH_ERROR } from "./types";

export const signin = (formProps, callback) => async (dispatch) => {
  let toastId = toast.info(<h2>Authentication is in progress...</h2>, {
    autoClose: false,
  });
  const data = JSON.stringify(formProps);
  try {
    const response = await signinapi.post("", data, {
      params: {
        grant_type: "password",
        scope: "write",
        username: formProps.username,
        password: formProps.password,
      },
    });
    if (toast.isActive(toastId)) {
      toast.update(toastId, {
        render: <h2>Authenticated Successfuly</h2>,
        type: toast.TYPE.SUCCESS,
        autoClose: 2000,
      });
    }
    dispatch({ type: AUTH_USER, payload: response.data.access_token });
    localStorage.setItem("access_token", response.data.access_token);
    callback();
  } catch (e) {
    if (toast.isActive(toastId)) {
      toast.update(toastId, {
        render: <h2>Authentication Failed</h2>,
        type: toast.TYPE.ERROR,
        autoClose: 2000,
      });
    }
    dispatch({ type: AUTH_ERROR, payload: "Invalid login credentials" });
  }
};
export const signout = () => {
  localStorage.removeItem("access_token");
  return {
    type: AUTH_USER,
    payload: "",
  };
};
export const signup = (formProps, callback) => async (dispatch) => {
  let toastId = toast.info(<h2>Signup is in progress...</h2>, {
    autoClose: false,
  });
  const data = JSON.stringify(formProps);
  try {
    const response = await api.post("/users/create/", data);
    if (toast.isActive(toastId)) {
      toast.update(toastId, {
        render: (
          <h2>Signup Successful, Please activate your account and Signin</h2>
        ),
        type: toast.TYPE.SUCCESS,
        autoClose: 2000,
      });
    }
    callback();
  } catch (e) {
    if (toast.isActive(toastId)) {
      toast.update(toastId, {
        render: <h2>Signup Error, Please try again</h2>,
        type: toast.TYPE.ERROR,
        autoClose: 2000,
      });
    }
    dispatch({ type: AUTH_ERROR, payload: "Signup Error" });
  }
};
